.message {
	position: relative;
	padding-bottom: rem(120);
	background: url(../img/section8/bg.jpg) no-repeat;
	background-size: cover;

	&::after {
		content: "";
		position: absolute;
		right: 0;
		bottom: 0;
		background: url(../img/section8/right_tree.png) no-repeat;
		width: rem(942);
		height: rem(537);
	}

	.container {
		display: flex;
		// position: relative;
	}

	&__left {
		margin-top: rem(72);
		margin-right: rem(188);

		display: flex;
		flex-direction: column;

		.section_header {
			margin-bottom: rem(50);

			.title-h2 {
				margin-bottom: rem(34);
			}
		}

		.item {
			display: flex;
			align-items: center;
			align-self: flex-start;
			margin-bottom: rem(30);

			&:last-child {
				margin-bottom: 0;
			}

			&__image {
				width: rem(38);
				height: rem(38);
				flex: 0 0 rem(38);
				border-radius: 50%;

				display: flex;
				align-items: center;
				justify-content: center;

				background-color: var(--c-green);

				margin-right: rem(30);
			}

			span {

			}
		}
	}

	&__right {
		position: relative;
		z-index: 2;
		
		.form {
			width: rem(460);
			padding: rem(64) rem(50);
			background-color: white;
			box-shadow: 0px 12px 21px -15px rgba(102, 102, 102, 0.25);

			&__title {
				margin-bottom: rem(32);
				line-height: 150%;
				font-size: rem(21);
				font-weight: 600;
			}

			.field-set {
				margin-bottom: rem(40);

				&:last-child {
					margin-bottom: rem(36);
				}
			}

			.field-button {
				> span {
					font-size: rem(12);
					line-height: 130%;
					color: #97A3B4;
					max-width: rem(228);
					display: block;
				}
			}

			.button_green {
				margin-bottom: rem(26);
				width: 100%;

				&:hover {
					span {
						color: var(--c-green);
					}
				}
			}
		}
	}
}