.diving {
	position: relative;
	padding-top: rem(152);

	padding-bottom: rem(225);

	.section_header {
		.title-h2 {
			margin-bottom: rem(32);
		}

		.text-p {
			margin-top: rem(16);
			max-width: rem(568);
		}
	}

	.grid_slider {
		margin-top: rem(78);
		margin-left: rem(184);
		display: grid;
		grid-template-columns: calc((100% - 1145px)/2) 0fr 10fr calc((100% - 1145px)/2);

		.buffer {
			grid-column: span 1;
		}
		.slider {
			grid-column: 2 / sp;

			.swiper-slide {
				opacity: .3;
			}
			.swiper-slide-active {
				opacity: 1;
			}
		}
	}

	&__control {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%);
		z-index: 2;

		width: 100%;
		display: flex;
		justify-content: space-between;

		.slider_bullet {
			position: absolute;
			top: rem(-70) !important;
			left: 51% !important;
			display: flex;

			.swiper-pagination-bullet {
				background-color: transparent !important;
				display: flex !important;
				align-items: center;
				font-size: rem(14);
				color: #E2E2E2;
				opacity: 1 !important;

				width: auto !important;
				height: auto !important;

				margin: 0 rem(20) 0 0 !important;

				transition: var(--transition) !important;

				&:hover {
					color: var(--c-text);
				}
				
				&:last-child {
					&::after {
						display: none;
					}
				}
				
			}

			.swiper-pagination-bullet-active {
				color: var(--c-text);
				margin: 0 !important;

				&::after {
					content: "";
					flex: 0 0 rem(33);
					width: rem(33);
					height: rem(1);
					background-color: #E2E2E2;
					margin: 0 rem(14);
				}
			}
		}

		.button_slide_prev, .button_slide_next {
			position: absolute;
			top: rem(32);
		}

		.button_slide_prev {
			left: 0;
		}

		.button_slide_next {
			right: 0;
		}
	}

	&__item {
		display: flex;
		max-width: rem(957);

		&_left {
			flex: 0 0 rem(460);
			max-width: rem(460);

			margin-right: rem(30);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&_right {
			display: flex;
			flex-direction: column;
			margin-top: rem(98);

			.title {
				color: var(--c-blue);
				font-size: rem(21);
				line-height: 150%;
				font-family: var(--f-prata);
				margin-bottom: rem(16);
			}

			.text-p {

			}
		}
	}
}

