.modal_overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(45, 45, 45, .4);
	backdrop-filter: blur(20px);
	z-index: 100;

	display: flex;
	align-items: center;
	justify-content: center;

	display: none;

	.modal_wrap {
		position: relative;
		width: rem(574);
		background-color: white;

		display: flex;
		flex-direction: column;

		@include min768 {
			width: 100%;
			height: 100%;
		}

		.modal_close {
			position: absolute;
			top: rem(30);
			right: rem(30);

			svg {
				path {
					stroke: #484642;
				}
			}

			&:hover {
				svg {
					path {
						stroke: var(--c-green);
					}
				}
			}

			@include min768 {
				top: rem(32);
				right: rem(32);

				svg {
					width: rem(16) !important;
					height: rem(16) !important;
				}
			}
		}

		.title-h2 {
			text-align: center;
			font-weight: normal;
			font-size: rem(36);
			color: #443377;
			margin-bottom: rem(52);
		}


	}

	.modal_wrap_success {
		padding-top: rem(100);
		padding-bottom: rem(84);

		align-items: center;
		justify-content: center;

		width: rem(620);

		@include min768 {
			width: rem(330);
			height: rem(322);

			padding-top: rem(82);
			padding-bottom: rem(56);

			padding-left: rem(24);
			padding-right: rem(24);
		}

		.title-h2 {
			margin-bottom: rem(8);
			@include min768 {
				font-size: rem(24);
			}
		}

		.text-p {
			max-width: rem(359);
			text-align: center;
			margin-bottom: rem(42);

			@include min768 {
				max-width: rem(256);
				font-size: rem(12);
			}
		}

		.button_green {

			&:hover {
				span {
					color: var(--c-green);
				}
			}

			@include min768 {
				width: 100%;
			}
		}
		
	}
}

.modal_overlay.active {
	display: flex;
}

.modal_phone {
	.modal_wrap {
		
		padding: rem(100) rem(75) rem(90) rem(110);

		@include min768 {
			padding: rem(64) rem(32) rem(72) rem(32);
		}

		.title-h4 {
			margin-bottom: rem(34);

			@include min768 {
				margin-bottom: rem(22);
			}
		}

		.text {
			margin-bottom: rem(72);
			padding-right: rem(35);

			@include min768 {
				padding-right: 0;
				margin-bottom: rem(48);
			}
		}

		.form {
			padding-right: rem(35);

			@include min768 {
				padding-right: 0;
			}
		}

		.field-set {
			margin-bottom: rem(45);

			@include min768 {
				margin-bottom: rem(32);
			}

			.field {
				font-size: rem(16);

				@include min768 {
					font-size: rem(14);
				}
			}
		}
		
		.field-button {
			display: flex;
			flex-direction: column;
			align-items: center;
			button {
				width: 100%;
				// height: rem(67);
				justify-content: center;
				margin-bottom: rem(16);

				&:hover {
					span {
						color: var(--c-green);
					}
				}
			}

			> span {
				font-size: rem(12);
				line-height: 140%;
				text-align: center;
				color: #8E8E93;
				display: flex;
				
				max-width: rem(228);

				span {
					text-decoration: underline;
				}
			}

		}
	}
}