.layouts {
	position: relative;
	padding-top: rem(110);
	padding-bottom: rem(210);

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;

		background: url(../img/section6/left_tree.png) no-repeat;
		width: rem(317);
		height: rem(618);
		z-index: -1;
		
		@include min1440 {
			display: none;
		}
	}

	.section_header {
		margin-top: rem(68);
		.title-h2 {
			margin-bottom: rem(32);
		}

		.text-p {
			margin-top: rem(16);
			max-width: rem(346);
		}
	}

	.grid_slider {
		display: grid;
		grid-template-columns: calc((100% - 1145px)/2) 0fr 10fr calc((100% - 1145px)/2);

		.buffer {
			grid-column: span 1;
		}
		.slider {
			grid-column: 2 / sp;

			display: flex;
			

			.swiper-slide {
				opacity: .3;

				align-items: flex-end;

				.layouts__item_info {
					display: none;
				}
			}
			.swiper-slide-active {
				opacity: 1;

				.layouts__item_info {
					display: flex;
				}
			}
		}
	}
	
	.container {
		position: relative;
	}

	&__nav {
		position: absolute;
		z-index: 2;
		left: rem(725);
		top: rem(-110);

		display: flex;
		align-items: center;

		.button_slide_prev {
			margin-right: rem(16);
		}
	}

	&__left {
		flex: 0 0 rem(346);
		max-width: rem(346);
		margin-right: rem(140);

		.bullet_slide {
			margin-top: rem(40);
			margin-bottom: rem(120);

			.swiper-pagination-bullet {
				background-color: rgba(127, 139, 155,.2) !important;
			}

			.swiper-pagination-bullet-active {
				background-color: var(--c-blue) !important;
			}
		}

		&_bottom {
			margin-top: rem(28);
			display: flex;
			flex-direction: column;

			b {
				font-size: rem(21);
				line-height: 150%;
				margin-bottom: rem(32);
			}

			p {
				font-size: rem(14);
				line-height: 150%;
				margin-bottom: rem(21);
				max-width: rem(152);
			}

			.button_green {
				align-self: flex-start;

				&:hover {
					span {
						color: var(--c-green);
					}
				}
			}
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		height: 100%;
		width: rem(420);

		.image {
			max-width: rem(420);
			height: rem(548);
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&_info {
			padding: rem(30) rem(25);
			background-color: var(--c-blue);

			display: flex;
			align-items: center;

			.item {
				display: flex;
				align-items: center;
				margin-right: rem(28);

				&:last-child {
					margin-right: 0;
				}

				

				span {
					font-size: rem(14);
					color: white;
					margin-left: rem(8);
				}
			}
		}
	}
}