.info {
	position: relative;
	padding-top: rem(143);
	padding-bottom: rem(164);

	&::after {
		content: "";
		position: absolute;
		right: rem(-200);
		bottom: rem(-285);
		background: url(../img/section4/right_bg.png) no-repeat;
		width: rem(1263);
		height: rem(938);

		@include min1440 {
			right: rem(-280);
		}
	}

	.section_header {
		margin-bottom: rem(28);

		.title-h2 {
			margin-bottom: rem(42);
		}
	}

	.subtitle {
		font-weight: 600;
		margin-bottom: rem(36);
		max-width: rem(415);
		display: inline-block;
		line-height: 150%;
	}

	.list {
		display: flex;
		flex-direction: column;

		max-width: rem(495);

		margin-bottom: rem(63);

		li {
			margin-bottom: rem(38);

			display: flex;

			&:last-child {
				margin-bottom: 0;
			}

			.image {
				margin-right: rem(35);

				flex: 0 0 rem(47);
				max-width: rem(47);

				display: flex;
				justify-content: center;
			}

		
		}
	}

	.button_green {
		&:hover {
			span {
				color: var(--c-green);
			}
		}
	}

	

}

.info__bottom {
	position: relative;
	// margin-top: rem(164);

	.section_header {
		margin-bottom: rem(28);

		.title-h2 {
			margin-bottom: rem(42);
		}
	}

	.text-p {
		padding-bottom: rem(50);
		max-width: rem(502);
	}
}


.map {
	position: relative;
	height: rem(563);

	img {
		width: 100%;
		height: rem(563);
		object-position: top;
		object-fit: cover;
	}


	.container {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		height: 100%;
	}

	&__item {
		position: absolute;
		top: rem(136);
		left: rem(500);
		display: flex;
		flex-direction: column;
		align-items: center;

		svg {

		}

		span {
			background-color: var(--c-blue);

			padding: rem(15) rem(30);

			font-family: var(--f-prata);
			color: white;
		}
	}

}