.section_card {
	position: relative;

	padding-bottom: rem(190);

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		right: 0;
		background: url(../img/section2/right_tree.png) no-repeat;
		width: rem(696);
		height: rem(568);
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		background: url(../img/section2/left_tree.png) no-repeat;
		width: rem(544);
		height: rem(906);
		z-index: -1;
	}

	.section_header {
		margin-bottom: rem(54);
	}

	&__image {
		height: rem(543);
		margin-bottom: rem(77);

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__item {
		display: flex;

		.image {
			margin-right: rem(46);
		}


	}

	.swiper {
		.swiper-wrapper {

			@include max768 {
				flex-wrap: wrap;
				justify-content: space-between;

				.swiper-slide {
					margin-bottom: rem(83);

					&:first-child, &:nth-child(3) {
						flex: 0 0 rem(574);
						max-width: rem(574);
					}

					&:nth-child(2), &:nth-child(4) {
						flex: 0 0 rem(443);
						max-width: rem(433);
					}

					&:last-child {
						flex: 0 0 rem(562);
						max-width: rem(562);
					}
				}
			}
			
		}
	}
}