.container {
	max-width: 1145px;
	margin: 0 auto;

	@include min1200 {
		margin-left: 20px;
		margin-right: 20px;
	}
}

.container-max {
	max-width: 1730px;
	margin: 0 auto;
}

.section_header {
	display: flex;
	flex-direction: column;

	span {
		line-height: 150%;
		font-size: rem(14);
		font-weight: 600;
		text-transform: uppercase;
		color: var(--c-green);
		margin-bottom: rem(28);
	}

	.title-h2 {
		color: var(--c-blue2);
	}
}

// text

.title-h1 {
	font-size: rem(72);
	font-family: var(--f-prata);
	font-weight: normal;

	@include min768 {
		font-size: rem(40);
	}
}

.title-h2 {
	font-size: rem(36);
	font-family: var(--f-prata);

	@include min768 {
		font-size: rem(21);
	}
}

.title-h3 {
	font-size: rem(40);

	span {
		color: var(--c-accent);
	}
}

.title-h4 {
	font-size: rem(30);

	@include min768 {
		font-size: rem(24);
	}

	@include min375 {
		font-size: rem(20);
	}

	span {
		color: var(--c-accent);
	}
}

.title-h5 {
	font-size: rem(16);

	span {
		color: var(--c-accent);
	}
}

.font-sup {
	font-family: var(--f-sup);
	font-weight: 500;
	
	font-feature-settings: 'liga'off;
}

.text-p {
	line-height: 150%;
}

// buttons

.button_border_green {
	border: rem(1) solid var(--c-green);
	height: rem(58);
	padding: 0 rem(45);

	display: flex;
	align-items: center;
	justify-content: center;

	&:hover {
		background-color: var(--c-green);
	}

	span {
		font-size: rem(12);
		letter-spacing: rem(2);
		color: white;
		font-weight: bold;
		line-height: 148%;
		text-transform: uppercase;
	}
}

.button_green {
	height: rem(58);
	padding: 0 rem(45);
	background-color: var(--c-green);
	border: rem(1) solid var(--c-green);

	display: flex;
	align-items: center;
	justify-content: center;

	&:hover {
		background-color: transparent;
	}

	span {
		color: white;
		letter-spacing: rem(2);
		text-transform: uppercase;
		font-size: rem(12);
		font-weight: bold;
	}
}

.bullet_slide {
	display: flex;
	align-items: center;
	width: auto !important;
	bottom: auto !important;
	

	.swiper-pagination-bullet {
		width: rem(6) !important;
		height: rem(6) !important;
		opacity: 1;
		background-color: rgba(255,255,255,.4) !important;
		margin: 0 rem(10) 0 0 !important;
	}

	.swiper-pagination-bullet-active {
		width: rem(8) !important;
		height: rem(8) !important;
		background-color: white !important;
	}
}

.button_slide_prev, .button_slide_next {
	flex: 0 0 rem(72);
	width: rem(72);
	height: rem(72);
	border-radius: 50%;
	background-color: var(--c-green);

	display: flex;
	align-items: center;
	justify-content: center;
}

// field

.field-set {
	position: relative;

	.icon {
		position: absolute;
		top: rem(1);
		left: rem(1);

		svg {
			path {
				stroke: #B3B7BB;
			}
		}
	}

	.error-message {
		position: absolute;
		bottom: rem(-22);
		left: 0;

		font-size: rem(14);
		color: #FF5A5A;

		display: none;
	}

	.field {
		width: 100%;
		padding: 0 rem(20) rem(8) rem(30);
		border-bottom: rem(1) solid #CED2D7;
		background-color: transparent;
		color: #484642;
		font-size: rem(14);

		@include placeholder {
			color: #B3B7BB;
		}
	}
}

.field-set.error {

	.icon {
		svg {
			path {
				stroke: #FF5A5A;
			}
		}
	}

	.field {
		border-color: #FF5A5A;
		color: #FF5A5A;

		@include placeholder {
			color: #FF5A5A;
		}
	}
}

@keyframes pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(178, 104, 85, .7);
	}

	70% {
		box-shadow: 0 0 0 rem(15) rgba(178, 104, 85, 0);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(178, 104, 85, 0);
	}
}