.gallery {
	position: relative;
	// padding-top: rem(212);

	padding-bottom: rem(197);

	.section_header {
		text-align: center;
		margin-bottom: rem(64);
	}

	.swiper {
		height: rem(469);
		margin-bottom: rem(31);

		.swiper-slide {
			height: 100%;
			width: rem(950);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}


	.container {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-top: rem(-72);
		transform: translateX(-50%);

		z-index: 2;
		display: flex;
		justify-content: space-between;
		width: 100%;
		max-width: 1020px;

		// margin-left: rem(-36);

		.button_slide_prev, .button_slide_next {
			position: absolute;
			z-index: 3;
		}

		.button_slide_prev {
			left: 0;
		}
		.button_slide_next {
			right: 0;
		}
	}

	.bullet_slide {
		justify-content: center;
		margin-top: rem(30);

		.swiper-pagination-bullet {
			background-color: rgba(127, 139, 155,.2) !important;
		}

		.swiper-pagination-bullet-active {
			background-color: var(--c-blue) !important;
		}
	}
}