.slide_menu {
	position: fixed;
	top: 0;
	left: 0;

	width: 40%;
	height: 100%;

	z-index: 16;

	background-color: var(--c-blue);

	overflow-y: auto;

	// display: flex;
	flex-direction: column;
	align-items: flex-end;

	transition: var(--transition);

	transform: translateX(-100%);

	// padding-left: rem(32);

	@include min {
		left: auto;
		right: 0;
		width: rem(480);

		transform: translateX(100%);
	}

	@include min768 {
		width: rem(294);
	}

	&__close {
		position: absolute;
		top: rem(38);
		right: rem(30);

		display: flex;
		align-items: center;

		@include min768 {
			top: rem(35);
			right: rem(25);

			svg {
				width: rem(25) !important;
				height: rem(25) !important;
			}
		}

		span {
			font-size: rem(13);
			color: white;
			text-transform: uppercase;
			margin-right: rem(16);

			@include min768 {
				display: none;
			}
		}
	}

	&__wrap {
		width: rem(390);

		display: flex;
		flex-direction: column;

		// align-items: flex-end;

		// padding-left: rem(32);
		margin-left: auto;
		margin-top: rem(35);

		@include min768 {
			width: 100%;
			margin-top: rem(32);
			padding-left: rem(32);
		}
	}

	&__logo {
		margin-bottom: rem(100);
		align-self: flex-start;

		@include min768 {
			margin-bottom: rem(52);
			align-self: baseline;
		}
	}

	&__nav {

		ul {

			li {
				margin-bottom: rem(50);

				&:last-child {
					margin-bottom: 0;
				}

				a, button {
					position: relative;
					font-size: rem(12);
					text-transform: uppercase;
					color: white;
					letter-spacing: rem(2);

					@include min768 {
						color: white;
					}

					&::before {
						position: absolute;
						top: 50%;
						margin-top: rem(-8);
						left: 0;
						content: "—";
						margin-right: rem(3);

						transform: translateX(-16px);
						opacity: 0;

						transition: var(--transition);
					}

					&:hover {
						color: white;

						padding-left: rem(17);

						&::before {
							opacity: 1;
							transform: translateX(0px);
						}
					}
				}

				a.active, button.active {
					color: white;

					padding-left: rem(17);

					&::before {
						opacity: 1;
						transform: translateX(0px);
					}
				}
			}
		}
	}

	&__phone {
		display: none;

		@include min {
			margin-top: rem(46);
			display: flex;

			align-items: center;

			svg {
				margin-right: rem(13);
				path {
					fill: white;
				}
			}

			span {
				color: white;
			}
		}
	}
}

.slide_menu.active {
	transform: translateX(0%);
}