.header {
	position: relative;
	height: rem(814);

	background: url(../img/header_bg.jpg) no-repeat;
	background-size: cover;

	margin-bottom: rem(126);

	&::after {
		content: "";
		position: absolute;
		bottom: rem(-110);
		right: 0;
		background: url(../img/header_tree.png) no-repeat;
		width: rem(146);
		height: rem(364);
	}

	.container {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__top {
		display: flex;
		justify-content: space-between;
		padding-top: rem(25);


		&_burger {
			display: flex;
			align-items: center;

			.left {
				width: rem(56);
				height: rem(56);
				border-radius: 50%;
				background-color: var(--c-green);

				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;

				margin-right: rem(17);

				.line {
					width: rem(20);
					height: rem(1);
					background-color: white;
					margin-bottom: rem(4);

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			.right {
				font-size: rem(12);
				color: white;
				line-height: 140%;
				text-transform: uppercase;
			}
		}

		&_right {
			display: flex;
			align-items: center;

			.phone {
				display: flex;
				align-items: center;
				margin-right: rem(28);

				svg {
					margin-right: rem(12);
				}

				span {
					color: white;
				}
			}
		}
	}

	&__center {
		display: flex;
		flex-direction: column;
		align-items: center;
		
		.title-h1 {
			color: white;
			margin-bottom: rem(8);
		}

		.text-p {
			margin-bottom: rem(55);
			color: white;
		}
	}

	&__bottom {
		display: flex;
		justify-content: center;
		padding-bottom: rem(70);
		
		.item {
			display: flex;
			flex-direction: column;

			margin-right: rem(140);

			&:last-child {
				margin-right: 0;
			}

			&__top {
				font-size: rem(48);
				color: white;
				font-family: var(--f-prata);
				font-weight: normal;

			}

			&__bottom {
				font-size: rem(14);
				color: white;
				text-transform: lowercase;
			}
		}
	}
}