.advantages {
	position: relative;
	height: rem(776);
	background: url(../img/section3/image.jpg) no-repeat;

	padding-top: rem(148);

	.container {
		position: relative;
	}
	
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(68, 51, 119, .93);
		z-index: 0;
	}

	&__left, &__right {
		position: relative;
		z-index: 1;
	}

	&__left {
		// margin-top: rem(148);

		img {
			height: rem(500);
		}
	}

	&__right {
		position: absolute;
		top: rem(100);
		right: rem(70);
		width: rem(465);
		

		&_wrap {
			padding: rem(50) rem(70);
			background-color: white;
		}

		&_nav {
			margin-top: rem(12);

			display: flex;
			align-items: center;
			justify-content: flex-end;

			.bullet_slide {
				margin-right: rem(58);
			}

			.button_slide_prev {
				margin-right: rem(16);
			}
		}

		&_count {
			position: absolute;
			top: rem(-45);
			right: rem(-12);

			width: rem(80);
			height: rem(80);
			background-color: var(--c-green);

			display: flex;
			align-items: center;
			justify-content: center;

			.count {
				font-size: rem(24);
				color: white;
				font-family: var(--f-prata);
			}
		}

		&_title {
			margin-bottom: rem(28);

			.title-h2 {
				color: var(--c-blue2);
				margin-bottom: rem(16);
			}
		}

		.swiper-slide {
			b {
				display: block;
				margin-bottom: rem(12);

				font-weight: 600;
				font-size: rem(16);
				line-height: 150%;
			}

			ul {
				li {
					line-height: 130%;
					font-size: rem(16);
				}
			}

			.list {
				li {
					margin-bottom: rem(12);
				}
			}
		}
	}
}